@mixin ab($t, $l, $b, $r) {
    position: absolute;    
    @if $l != n { left: $l; }
    @if $t != n { top: $t; }
    @if $r != n { right: $r; }
    @if $b != n { bottom: $b; }
}
@mixin fixed($t, $l, $b, $r) {
    position: fixed;
    @if $l != n { left: $l; }
    @if $t != n { top: $t; }
    @if $r != n { right: $r; }
    @if $b != n { bottom: $b; }    
}
@mixin border-inline-start($value) {
    border-inline-start:$value;
}  
@mixin border-inline-end($value) {
    border-inline-end:$value;
}
@mixin rotate($rotate) { 
  transform: rotate($rotate);
}
@mixin padding-start($value) {
    padding-inline-start: $value;
}
@mixin padding-end($value) {
    padding-inline-end: $value;
}
@mixin margin-start($value) {
    margin-inline-start: $value;
}
@mixin margin-end($value) {
    margin-inline-end: $value;
}
@mixin text-right {
    text-align: end;
}
@mixin text-left {
    text-align: start;
}
@mixin left($l) {
  left: $l;
}
@mixin right($r) {
  right: $r;
}

.templateCustomers {
    .main-page {
        padding-bottom: 50px;
    }
    #main-content {
        contain: none;
    }
}
.page-heading {
    font-size: 26px;
    line-height: 1.3em;
    text-transform: uppercase;
    font-weight: 500;
    margin: 20px 0 30px;
}
.form-wrapper {
    .link-recover {
        color: var(--primary-color);
        margin-bottom: 20px;
        display: block;
    }
    >a.btn {
        min-width: 130px;
        text-align: center;
    }
}
.form-field {
    margin-bottom: 25px;
    input {
        background: var(--miscellaneous-color);
        border-radius: var(--border-radius);
    }
    button {
        font-family: inherit;
        min-width: 130px;
        @include margin-end(10px);
    }
}
.form-button-group {
    margin-bottom: 0;
    display: flex;
    flex-wrap: wrap;
    >a.btn {
        display: flex;
        align-items: center;
        justify-content: center;
    }
}
.first-block {
    padding-bottom: 30px;
    border-bottom: 1px solid var(--miscellaneous-color);
}
.error, .success {
    margin: 0 0 15px;
    display: flex;
    align-items: center;
    >svg {
        width: 20px;
        height: 20px;
        @include margin-end(8px);
    }
    span {
        line-height: 1.6em;
        display: block;
        color: var(--subtext-color);
    }
}
.errors {
    margin-bottom: 20px;
    color: var(--subtext-color);
    a {
        color: var(--primary-color);
    }
}
#RecoverEmail-email-error {
    margin-top: 15px;
}
.second-block {
    margin-bottom: 60px;
}
.account-detail {
    .h4 {
        position: relative;
        padding-bottom: 12px;
        margin-bottom: 25px;
        border-bottom: 1px solid var(--miscellaneous-color);
        &:before {
            content: "";
            @include ab(n,0,-1px,n);
            width: 80px;
            height: 2px;
            background: var(--primary-color);
        }
    }
    .account-name {
        font-size: 18px;
        font-weight: 500;
    }
    .logout {
        a {
         font-weight: 500;
         transition: 0.25s all ease-in-out;
        }
    }
}
.account-detail-inner {
    margin-bottom: 40px;
    >div {
        padding: 5px 0;
    }
    a {
       text-underline-offset: 1px;
       &:hover {
        text-decoration: underline;
        color: var(--primary-color);
       }
    }
}
.main-account {
    .h4 {
        position: relative;
        padding-bottom: 12px;
        margin-bottom: 25px;
        border-bottom: 1px solid var(--miscellaneous-color);
        &:before {
            content: "";
            @include ab(n,0,-1px,n);
            width: 80px;
            height: 2px;
            background: var(--primary-color);
        }
    }
}
.order-list {
    width: 100%;
    overflow: hidden;
    overflow-x: auto;
    margin-bottom: 20px;
    .new-address {
        justify-content: space-between;
        .h4 {
            flex: 1;
        }
    }
    .btn-wrapper {
        >button {
            display: flex;
            align-items: center;
            font-size: 14px;
            transition: unset;
        }
        svg {
            width: 10px;
            height: 10px;
            @include margin-end(10px);
        }
    }
    .addresses-wrapper {
        >ul >li {
            margin-bottom: 20px;
        }
    }
    .address-card {
        border: 1px solid var(--miscellaneous-color);
        padding: 30px;
        overflow: hidden;
        height: 100%;
        border-radius: var(--border-radius);
        .address-detail {
            p {
                margin-top: 0;
            }
        }
    }
    .card-inner {
        height: 100%;
        display: flex;
        flex-direction: column;
    }
    .address-button-group {
        margin-top: auto;
        button {
            width: 100%;
            justify-content: center;
        }
    }
    .label {
        color: var(--miscellaneous-color);
        background: var(--primary-color);
        width: 90px;
        height: 90px;
        display: flex;
        align-items: flex-end;
        justify-content: center;
        transform: rotate(45deg);
        @include right(-45px);
        top: -45px;
        padding: 6px;
        font-size: 12px;
    }
}
.order-history {
    @include text-left;
    border: 1px solid var(--miscellaneous-color);
    border-collapse: collapse;
    thead {
        th {
            padding: 15px;
            background: #f9f8f8;
            border-bottom: 1px solid var(--miscellaneous-color);
            @include text-left;
            &:not(last-child) {
                @include border-inline-end(1px solid var(--miscellaneous-color));
            }
        }
    }
    tbody {
        >tr {
            >td:not(last-child) {
                @include border-inline-end(1px solid var(--miscellaneous-color));
            }
        }
        >tr:not(:last-child) {
            border-bottom: 1px solid var(--miscellaneous-color);
        }
        td {
            padding: 15px;
        }
    }
}
.address-poup {
    h2 {
        font-size: 26px;
        margin-bottom: 15px;
    }
    .form-field {
        display: flex;
        align-items: center;
        margin-bottom: 5px;
        input[type="checkbox"] {
            padding: 0;
            appearance: auto;
            width: 16px;
            height: 16px;
            @include margin-start(108px);
            margin-top: 10px;
            + .label-field {
                flex: 1;
                margin-top: 10px;
                margin-bottom: 0;
                @include margin-start(8px);
            }
        }
    }
    form >div:last-of-type {
        width: 100%;
        text-align: center;
        display: block;
    }
    .label-field {
        flex: 0 0 108px;
        font-size: 13px;
        margin-bottom: 0;
    }
    .select-field {
        position: relative;
        width: 100%;
        select {
            border: 0;
            background: var(--miscellaneous-color);
            border-radius: var(--border-radius);
            height: 40px;
            padding: 5px 20px;
            border-radius: var(--border-radius);
        }
        >svg {
            @include ab(n,n,n,15px);
            height: 100%;
            pointer-events: none;
            path {
                fill: var(--text-color);
            }
        }
    }
    form {
        max-width: 450px;
    }
    div {
        button {
            font-size: 14px;
            margin-top: 15px;
        }
    }
}
.order-detail {
    width: 100%;
    overflow-x: auto;
    flex: 0 0 100%;
    max-width: 100%;
    margin-bottom: 30px;
}
.order-summary {
    margin-bottom: 30px;
    p {
        margin: 0;
        line-height: 1.8em;
    }
}
.order-billing-detail {
    flex: 0 0 100%;
    max-width: 100%;
    display: flex;
    flex-wrap: wrap;
    >div {
        flex: 0 0 100%;
        margin-bottom: 10px;
    }
    hr {
        display: none;
    }
}
.address-detail {
    p {
        line-height: 1.8em;
    }
}
.order-items-detail {
    margin-bottom: 10px;
    width: 100%;
    @include text-left;
    border: 1px solid var(--miscellaneous-color);
    border-collapse: collapse;
    .item-title {
        a {
            color: var(--text-color);
        }
    }
    thead {
        th {
            padding: 15px;
            background: #f9f8f8;
            border-bottom: 1px solid var(--miscellaneous-color);
            @include text-left;
            &:not(last-child) {
                @include border-inline-end(1px solid var(--miscellaneous-color));
            }
        }
    }
    tbody {
        >tr {
            >td:not(last-child) {
                @include border-inline-end(1px solid var(--miscellaneous-color));
            }
        }
        >tr:not(:last-child) {
            border-bottom: 1px solid var(--miscellaneous-color);
        }
        td {
            padding: 15px;
            @include text-left;
        }
        .price {
            color: var(--text-color);
        }
    }
    tfoot {
        tr {
            border-top: 1px solid var(--miscellaneous-color);
            >td:last-child {
                @include border-inline-start(1px solid var(--miscellaneous-color));
            }
        }
        td {
            padding: 15px;
        }
    }
    #RowTotal {
        font-weight: 500;
    }
    td[data-label="Total"] {
        font-weight: 500;
        color: var(--primary-color);
    }
}
.reset-password-form {
    padding-top: 50px;
    .form-button-group {
        margin: -4px -8px;
        .btn {
            margin: 4px 8px;
            flex: 1;
            min-width: 200px;
        }
    }
}
@media (min-width: 750px) {
    .block {
        padding-bottom: 0;
    }
    .first-block {
        @include padding-end(40px);
        @include border-inline-end(1px solid var(--miscellaneous-color));
        border-bottom: 0;
        margin-bottom: 60px;
    }
    .second-block {
        @include padding-start(40px);
    }
    .account-detail {
        flex: 0 0 250px;
        max-width: 250px;
    }
    .order-billing-detail {
        >div {
            flex: 1;
        }
    }
    .main-account {
        flex: 1;
        max-width: 100%;
    }
    .order-history {
        width: 100%;
    }
    .order-list {
        overflow: hidden;
    }
    .popup-gl {
       form >div:last-of-type {
        @include padding-start(108px);
        text-align: unset;
       }
    }
    .reset-password-form {
        .form-button-group {
            .btn {
                flex: 0 0 200px;
                min-width: 200px;
            }
        }
    }
}
@media (min-width: 1000px) {
    .account-detail {
        flex: 0 0 300px;
        max-width: 300px;
    }
}